/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
/*--------------------------------------------------------------
# Inner Circle Event Cards

1. Mobile
2. Desktop
--------------------------------------------------------------*/
#events .card-ic-event,
#sidebar-post-meta .card-ic-event,
#event-breakout .card-ic-event {
		margin-bottom: 2rem;
}
#events .card-ic-event .card-header,
#sidebar-post-meta .card-ic-event .card-header,
#event-breakout .card-ic-event .card-header {
		padding: 1rem 1.5rem 0 1.5rem;
}
#events .card-ic-event .card-details,
#sidebar-post-meta .card-ic-event .card-details,
#event-breakout .card-ic-event .card-details {
		padding: 0 1.5rem 1rem 1.5rem;
}
#events .card-ic-event .card-details svg,
#sidebar-post-meta .card-ic-event .card-details svg,
#event-breakout .card-ic-event .card-details svg {
		margin-right: 0.5rem;
}
#events .card-ic-event .card-footer a,
#sidebar-post-meta .card-ic-event .card-footer a,
#event-breakout .card-ic-event .card-footer a {
		font-size: 14px;
		color: #747474;
		margin-right: 1rem;
}

@media (min-width: calc(992px)) {
		#events {
				padding: 0 0 0 2rem;
		}
}
#event-breakout {
		background-color: #e8e8e8;
		padding: 6rem 0;
		max-width: 1920px;
}
#event-breakout h2 {
		margin-left: 6px;
		margin-bottom: 2rem;
}
#event-breakout .event-card-container {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: 1fr;
		gap: 2rem;
}

/*--------------------------------------------------------------
# Inner Circle - Single Post

1. Override existing styles from parent theme SASS
2. File attachment
--------------------------------------------------------------*/
.single main {
		padding: 0;
}

/*--------------------------------------------------------------
3. Hero + title layout
--------------------------------------------------------------*/
#single-hero {
		margin-bottom: 2rem;
		background-color: #e8e8e8;
}
#single-hero .title-wrap {
		color: #191919;
		padding: 2rem 2rem 0rem 2rem;
}
#single-hero .title-wrap h1 {
		/* Like an H2, mobile */
		margin: 0;
		font-size: 2rem;
		line-height: 2.125rem;
		letter-spacing: -0.07rem;
}
#single-hero .tag-wrap {
		color: #191919;
		padding: 1rem 2rem 2rem 2rem;
}
#single-hero .tag-wrap .entry-meta {
		margin-bottom: 1rem;
}
#single-hero .tag-wrap .tags-links {
		margin-bottom: 0;
}
#single-hero .tag-wrap .tags-links a {
		color: #191919;
}
#single-hero .tag-wrap svg {
		margin-right: 0.5rem;
}
#single-hero .category-wrap {
		background-color: #191919;
		color: #ffffff;
		height: 3rem;
		padding-left: 2rem;
		padding-right: 2rem;
		display: flex;
		justify-content: flex-start;
		align-items: center;
}
#single-hero .category-wrap .category-tags .btn {
		background-color: #191919;
		color: #ffffff;
		text-transform: uppercase;
		margin-top: 0;
}

@media (min-width: calc(992px)) {
		#single-hero {
				position: relative;
				max-width: 100%;
				margin-bottom: 4rem;
				display: grid;
				grid-template-columns: auto minmax(0px, 800px) 400px auto;
				grid-template-rows: auto auto;
		}
		#single-hero .title-wrap {
				grid-row: 1;
				grid-column: 2/span 2;
				padding: 2rem 8rem 0 0;
		}
		#single-hero .title-wrap h1 {
				/* Like an H2 */
				margin: 0;
				font-size: 2.5rem;
				line-height: 2.75rem;
				letter-spacing: -0.0875rem;
		}
		#single-hero .tag-wrap {
				grid-row: 2;
				grid-column: 2;
				padding: 1rem 0 1rem 0;
		}
		#single-hero .tag-wrap .entry-meta {
				margin-bottom: 0.5rem;
		}
		#single-hero .category-wrap {
				grid-row: 2;
				grid-column: 3;
				align-self: end;
				margin-bottom: -1.5rem;
				margin-left: calc(2rem + 12px);
		}
		#single-hero .category-wrap .category-tags {
				text-align: center;
				margin: 0;
		}
}
/*--------------------------------------------------------------
4. content-wrap, adjust for desktop layout.
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
		main > .container.content-wrap {
				display: block;
				padding-left: 0;
				padding-right: 0;
		}
}
/*--------------------------------------------------------------
5. File Attachment (.downloads)
--------------------------------------------------------------*/
.downloads {
		flex-direction: column;
		position: relative;
		margin: 3rem 0;
		padding-top: 3rem;
}
.downloads:before {
		content: "";
		height: 1px;
		width: 100%;
		max-width: 16rem;
		display: block;
		position: absolute;
		top: 0;
		background-color: #d0d0d0;
}
.downloads img {
		margin-bottom: 1.5rem;
}
.downloads img.attachment-icon {
		padding: 1.5rem;
		border: 1px solid #e8e8e8;
}
.downloads .media-body p:first-child {
		font-weight: 700;
}

@media (min-width: calc(992px)) {
		.downloads {
				flex-direction: row;
		}
		.downloads img {
				margin-right: 1.5rem;
		}
}
/*--------------------------------------------------------------
5. Featured Image
--------------------------------------------------------------*/
.wp-block-image.wp-post-image {
		margin-bottom: 3rem;
}

/*--------------------------------------------------------------
5. Sidebar Post Meta Card
--------------------------------------------------------------*/
.post-meta svg {
		margin-right: 0.5rem;
}

/*--------------------------------------------------------------
# Inner Circle - Tag.php

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/
.archive.tag #tag-hero .term-description {
		font-size: 1.25rem;
		padding-right: 2rem;
}
.archive.tag #tag-hero img {
		display: none;
}
.archive.tag #tag-hero .container {
		border-bottom: 1px solid #e8e8e8;
		padding-top: 2rem;
		padding-bottom: 2rem;
		margin-bottom: 2rem;
}
.archive.tag article {
		border-bottom: 1px solid #e4e4e4;
		padding-bottom: 1rem;
		margin-bottom: 1rem;
}
.archive.tag article:last-child {
		border-bottom: 0;
}
.archive.tag .entry-footer {
		display: inline-block;
}
.archive.tag .entry-footer svg {
		margin-right: 0.5rem;
}
.archive.tag .entry-footer svg.fa-clock,
.archive.tag .entry-footer svg.fa-map-marker-alt {
		margin-left: 1.5rem;
}
.archive.tag .entry-footer .card-tags {
		margin-top: 1rem;
		padding: 0;
}

/*--------------------------------------------------------------
2. Desktop
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
		.archive.tag #tag-hero .container {
				border-bottom: 2px solid #e8e8e8;
				padding-top: 4rem;
				padding-bottom: 4rem;
				margin-bottom: 4rem;
		}
		.archive.tag #tag-hero img {
				display: block;
				margin-top: 1rem;
		}
		.archive.tag article {
				padding-bottom: 4rem;
				margin-bottom: 2rem;
		}
}
/*--------------------------------------------------------------
# Inner Circle - category.php
--------------------------------------------------------------*/
/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/
.archive.category .entry-header {
		display: block;
		padding: 0;
}
.archive.category .entry-header .entry-title {
		border-bottom: 0;
}
.archive.category aside {
		display: none;
}
.archive.category .archive-loop article {
		margin-bottom: 2rem;
		padding-bottom: 2rem;
		border-bottom: 1px solid #d0d0d0;
}
.archive.category span.page-link {
		padding-left: 0.75rem;
		padding-right: 0.75rem;
}
.archive.category .ic-post-columns {
		margin-top: 1rem;
		row-gap: 3rem;
}
.archive.category .entry-footer {
		display: inline-block;
}
.archive.category .entry-footer svg {
		margin-right: 0.5rem;
}
.archive.category .entry-footer svg.fa-clock,
.archive.category .entry-footer svg.fa-map-marker-alt {
		margin-left: 1.5rem;
}

/*--------------------------------------------------------------
2. Desktop
--------------------------------------------------------------*/
@media (min-width: calc(768px)) {
		.archive.category div[class^=uds-hero] + .container {
				margin-top: 4rem;
		}
		.archive.category .category-header {
				padding-top: 4rem;
		}
		.archive.category .category-header + .container {
				margin-top: 4rem;
		}
		.archive.category .archive-loop {
				padding-right: 4rem;
		}
		.archive.category aside {
				display: block;
		}
}
/*--------------------------------------------------------------
3. Calendar
   - UDS Calendar component only renders the mobile view at a mobile
   viewport size. Including the mobile rules here with a class declaration
   to force this to render mobile-only in the col-md-3 placeholder.
--------------------------------------------------------------*/
#calendar.force-mobile .calendar-grid {
		padding-left: 0;
		padding-right: 0;
		text-align: center;
}
#calendar.force-mobile .calendar-grid .heading {
		border-bottom: 1px solid #747474;
		padding-bottom: 0.5rem;
}
#calendar.force-mobile .calendar-grid .heading.desktop {
		display: none;
}
#calendar.force-mobile .calendar-grid .heading.mobile {
		display: grid;
}
#calendar.force-mobile .calendar-grid .body {
		border: unset;
}
#calendar.force-mobile .calendar-grid .body .calendar-item {
		padding: 1rem 0.5rem 0.5rem;
		border: unset;
}

/*--------------------------------------------------------------
# Inner Circle Calendar Grid  & Layout

1. Layout
2. Calendar Grid styles
3. Event Preview/Details
--------------------------------------------------------------*/
#calendar-wrapper.grid {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr auto;
		grid-row-gap: 2rem;
}

#calendar {
		grid-row: 1;
}

#event-preview {
		display: none;
}

@media (min-width: calc(768px)) {
		#event-preview {
				display: block;
				grid-row: 2;
		}
}
@media (min-width: calc(992px)) {
		#calendar-wrapper.grid {
				display: grid;
				grid-template-columns: 2fr 1fr;
				grid-template-rows: 84px 1fr;
				grid-column-gap: 0px;
				grid-row-gap: 0px;
		}
		#calendar {
				grid-row: 1/span 2;
				grid-column: 1;
		}
		#event-preview {
				display: block;
				grid-row: 2;
				grid-column: 2;
		}
}
/*--------------------------------------------------------------
2. Calendar Grid styles
--------------------------------------------------------------*/
#calendar .fc-header-toolbar {
		flex-direction: column;
		align-items: flex-start;
}
#calendar .fc-header-toolbar .fc-button {
		background-color: #8c1d40;
		border-color: #8c1d40;
		border-radius: 6400px;
		font-size: 0.875rem;
		padding: 0.5rem 1rem;
}
#calendar .fc-header-toolbar .fc-button-group {
		font-size: 0.875rem;
}
#calendar .fc-header-toolbar .fc-button-group .fc-button:first-child {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		padding-left: 1rem;
}
#calendar .fc-header-toolbar .fc-button-group .fc-button:last-child {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		padding-right: 1rem;
}
#calendar .fc-header-toolbar .fc-toolbar-title {
		margin-bottom: 1rem;
}

@media (min-width: calc(992px)) {
		#calendar .fc-header-toolbar {
				flex-direction: row;
				align-items: center;
		}
		#calendar .fc-header-toolbar .fc-button {
				font-size: 1rem;
		}
		#calendar .fc-header-toolbar .fc-button-group {
				font-size: 1rem;
		}
		#calendar .fc-header-toolbar .fc-toolbar-title {
				margin-bottom: 1rem;
		}
}
.fc#calendar .fc-daygrid .fc-scrollgrid tbody {
		background-color: #ffffff;
}
.fc#calendar .fc-daygrid .fc-col-header th {
		text-align: left;
		background-color: #fafafa;
}
.fc#calendar .fc-daygrid .fc-col-header .fc-col-header-cell-cushion {
		color: #191919;
		text-decoration: none;
		font-size: 14px;
		font-weight: 400;
}
.fc#calendar .fc-daygrid .fc-daygrid-day-top {
		flex-direction: row;
}
.fc#calendar .fc-daygrid .fc-daygrid-day-top a {
		text-decoration: none;
		color: #191919;
}
.fc#calendar .fc-daygrid .fc-day-today {
		background-color: rgba(255, 198, 39, 0.15);
}

/*--------------------------------------------------------------
3. Event Preview
--------------------------------------------------------------*/
#event-preview .post-title {
		margin: 3rem 2rem;
		position: relative;
}
#event-preview .post-title:after {
		content: "Post title";
		font-size: 1rem;
		font-weight: 700;
		line-height: 1.5rem;
		letter-spacing: -0.015rem;
		box-shadow: -0.15em 0 0 #191919, 0.15em 0 0 #191919;
		padding-left: 0.15em;
		padding-right: 0.15em;
		background: #191919;
		color: #fafafa;
		position: absolute;
		top: -2rem;
		margin-bottom: 1rem;
}
#event-preview .event-details {
		background-color: #fafafa;
		padding: 3rem;
		border: 1px solid #e8e8e8;
}
#event-preview .event-details h4 {
		margin-top: 0;
}
#event-preview .event-details .cal-link {
		font-size: 14px;
		color: #747474;
		margin-right: 1rem;
}
#event-preview svg.svg-inline--fa {
		margin-right: 1rem;
}

@media (min-width: calc(992px)) {
		#event-preview .event-details {
				margin-left: -1px;
		}
}
/*--------------------------------------------------------------
1. Misc Fixes
--------------------------------------------------------------*/
.pagination span.page-link {
		padding: 0.5rem 0.75rem;
}